import Vue from 'vue'
import App from './App.vue'
import router from './router'
require('./assets/css/base.css')

Vue.config.productionTip = false

if ('serviceWorker' in navigator) {
  navigator.serviceWorker.register('/sw.js', { scope: '/' })
}

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
